import React from 'react';
import './Header.css';


class Header extends React.Component {

    onAboutClick = (e) => {
        if (this.props.showAbout) {
            const aboutBackdrop = document.getElementById('about-backdrop');

            aboutBackdrop.addEventListener('animationend', (eventObject) => {
                if (getComputedStyle(eventObject.target).opacity == 0) {
                    this.props.onAboutClick(false);
                }
            });

            aboutBackdrop.className += " info-backdrop-end-animation"
        } else {
            if (e.target.id != "title")
                this.props.onAboutClick(true);
        } 
    }

    render() {
        return (
            <div className="header"> 
                <nav className="site-nav wrapper">
                    <a className="site-title page-link active" id="title" onClick={this.onAboutClick}>Vores Strøm</a>
                    <a className="page-link " onClick={this.onAboutClick}>Om projektet</a>
                </nav>
            </div>
        );
    }
}




export default Header;